define('dummy/tests/unit/components/paper-radio-test', ['ember', 'ember-qunit'], function (Ember, ember_qunit) {

  'use strict';

  ember_qunit.moduleForComponent('paper-radio', 'PaperRadioComponent', {});

  ember_qunit.test('it renders', function (assert) {
    assert.expect(2);

    // creates the component instance
    var component = this.subject();
    assert.equal(component._state, 'preRender');

    // appends the component to the page
    this.render();
    assert.equal(component._state, 'inDOM');
  });

  ember_qunit.test('should set checked css class', function (assert) {
    var component = this.subject();
    this.render();

    Ember['default'].run(function () {
      component.set('checked', true);
    });

    assert.ok(this.$().hasClass('md-checked'));
  });

  ember_qunit.test('it updates when clicked, and triggers the `changed` action', function (assert) {
    var changedActionCallCount = 0;
    var component = this.subject({
      value: 'component-value',
      selected: 'initial-selected-value',
      changed: 'changed',
      targetObject: Ember['default'].Controller.createWithMixins({
        actions: {
          changed: function changed() {
            changedActionCallCount++;
          }
        }
      })
    });
    this.append();

    assert.equal(changedActionCallCount, 0);
    assert.equal(component.$().hasClass('md-checked'), false);

    Ember['default'].run(function () {
      component.$().trigger('click');
    });

    assert.equal(component.$().hasClass('md-checked'), true, 'updates element property');
    assert.equal(component.get('checked'), true, 'updates component property');
    assert.equal(component.get('selected'), 'component-value', 'updates selected ');

    assert.equal(changedActionCallCount, 1);
  });

  ember_qunit.test('it is possible to check and set selected property to value', function (assert) {
    var component = this.subject();

    assert.ok(!component.get('selected'));

    var value = 'some value';

    Ember['default'].run(function () {
      component.set('value', value);
    });

    this.$().trigger('click');

    assert.equal(component.get('selected'), value);
  });

  ember_qunit.test('it isn\'t possible to uncheck and set selected property to null', function (assert) {
    var component = this.subject();

    var value = 'some value';

    Ember['default'].run(function () {
      component.set('value', value);
      component.set('selected', value);
    });

    this.$().trigger('click');

    //still has value
    assert.equal(component.get('selected'), value);
  });

  ember_qunit.test('it is possible to uncheck and set selected property to null if toggle is true', function (assert) {
    var component = this.subject();

    var value = 'some value';

    Ember['default'].run(function () {
      component.set('value', value);
      component.set('selected', value);
      component.set('toggle', true);
    });

    this.$().trigger('click');

    assert.equal(component.get('selected'), null);
  });

  ember_qunit.test('it isn\'t possible to check a disabled radio button', function (assert) {
    var component = this.subject();

    Ember['default'].run(function () {
      component.set('disabled', true);
      component.set('selected', false);
    });

    this.$().trigger('click');

    assert.equal(component.get('selected'), false);
  });

  // specify the other units that are required for this test
  // needs: ['component:foo', 'helper:bar']

});